body{
  margin: 0;
}
.app-gradient {
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box!important;
  color: white;
}

.slick-arrow::before {
  content: "❬";
  margin: auto;
  color: white !important;
}

.slick-arrow:hover {
  background: transparent linear-gradient(202deg, #78539C 0%, #6D3085 50%, #393185 100%) 0% 0% no-repeat padding-box !important;
}

.slick-arrow.slick-prev::before {
  content: "❮";
  font-size: 20px;
}

.slick-arrow.slick-next::before {
  content: "❯";
  font-size: 20px;

}

.slick-prev {
  left: -79px;
}


.card-meta-desc{
  margin: 0;
  color: #000;
}

.card-title{
  font-size: 20px;
  font-weight: 600;
}

.card-subTitle{
  font-size: 16px;
  font-weight: 500;
}

.card-priceTag{
  color: #7e7e7e;
  font-size: 15px;
}

.card-priceTag span{
  color: #5F5F5F;
  font-size: 18px;
  font-weight: 700;
  
}

.cardBrand{
  font-size: 18px;

}

.cardIcon{
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
  color: #613B8C;
  cursor: pointer;

}
.horizintal-category-menu{
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box!important;
  color: white;
}
.ant-menu-item-group-list span{
  color: black!important;
}

.horizintal-category-menu li.ant-menu-item{
  padding: 0 40px;
}
.horizintal-category-menu li.ant-menu-item span, .horizintal-category-menu li.ant-menu-overflow-item span{
  color: white!important;
  font-size: 18px;
}

.box-wrapper {
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box;
  padding: 2rem;
}

.slick-slider{
  padding-left:50px;
}
.slick-dots li button:before {
  color: rgb(179, 178, 178) !important;
  font-size: 15px !important;
  margin-top: 10px;
}
.slick-dots button {
  position: relative !important; 
  align-items: center !important;
  right: 50px;
}

.slick-dots li.slick-active button:before {
  color: white !important;
  /* font-size: 12px !important; */
}

.head {
  display: flex;
  justify-content: space-between;

}